import OpenList from "../OpenList";


const Planimetry = ({page}) => { 

    const notext = '    ';

    const repeatable_facts = {
        ' /': {
            name: '  ',
            text: '         ,<br>'
            +'  ,     '
        },
        ' /': {
            name: '   ',
            text: '      '
        },
        ' ': {
            name: ' ',
            text: `   \\(a ~-\\)   \\(\\alpha ~-\\)   ,  \\(D ~-\\)   ,<br>
               \\(\\displaystyle\\frac{a}{\\sin{\\alpha}} = D\\)`
        },
        '': {
            name: ' ',
            text: '    2  '
        },
        ' ': {
            name: '   ',
            text: '         '
        },
        ' ': {
            name: '   ',
            text: '        '
        },
        '': {
            name: ' ',
            text: '   ,   ,<br>'
            +'        '
        },
        ' ': {
            name: '   ',
            text: `    ,<br>
                   `
        },
        '  ': {
            name: '  ',
            text: '      ,   '
        },
        '  ': {
            name: '  ',
            text: '       ,   ,<br>'
                +'     '
        },
        '': {
            name: '  ',
            text: `  ,          :<br><br>
            1.  2 <br>
            2.    2     <br>
            3. 3  `
        },
        '': {
            name: '     ',
            text: '    ,    <br>'+
            '  ,  '
        },
        ' ': {
            name: '    ',
            text: '        '
        },
        ' ': {
            name: '  ',
            text: '      <br>'+
            '    '
        },
        ' ': {
            name: '  ',
            text: '         '
        }
    }
    
    const facts = {
        '': [
            {
                name: '',
                text: ' -   ,      '
            },
            {
                name: ' ',
                text: '  - ,        '
            },
            {
                name: ' ',
                text: '  -        '
            },
            {
                name: '',
                text: '  - ,       '
            },
            {
                name: ' ',
                text: '  - ,      '
            },
            {
                name: ' ',
                text: '  - ,     ,   '
            },
            {
                name: ' ',
                text: '  - ,    '
            }
        ],
        ', ': [
            {
                name: '  ',
                text: '           2  1,   '
            },
            repeatable_facts[' /'],
            repeatable_facts[' /'],
            repeatable_facts[''],
            repeatable_facts[" "],
            repeatable_facts[' '],
            repeatable_facts[''],
            {
                name: '  ',
                text: '        '
            },
            {
                name: '  ',
                text: '     ,      '
            },
            repeatable_facts[' ']
        ],
        '': [
            repeatable_facts[' '],
            {
                name: '  ',
                text: '     2 ,  <br>'+
                '  ,     '
            },
            {
                name: ' ',
                text: notext
            },
            {
                name: ' ',
                text: notext
            },
            repeatable_facts[' '],
            repeatable_facts['  '],
            repeatable_facts['  '],
            repeatable_facts[''],
            {
                name: '   ',
                text: '       '
            }
        ],
        '': [
            {
                name: ' ',
                text: '  '
            },
            {
                name: ' ',
                text: '          '
            },
            {
                name: ',  ,  ',
                text: `           : <br><br>
                1.    <br>
                2.     <br>
                3.      \\(180^{\\circ}\\),<br><br>
                   
                `
            },
            {
                name: '  ',
                text: '   \\(n\\)-  \\(180^{\\circ}\\cdot (n-2)\\)'
            }
        ],
        '':[
            {
                name: '  ',
                text: `
                1.    <br>
                2.   <br>
                3.     \\(180^{\\circ}\\)`
            },
            {
                name: ' ',
                text: `        <br>
                    ,        `
            },
            repeatable_facts[' ']
        ],
        ' ': [
            {
                name: '  ',
                text: `  ,          :<br><br>
                1.  2     <br>
                2.    2    <br>
                3.  3 `
            },
            repeatable_facts[''],
            {
                name: '  ',
                text: '    \\(180^{\\circ}\\)'
            },
            {
                name: ' ',
                text: ` ${page.tex('a,~b,~c ~-')}    ${page.tex('\\alpha ~-')}    ${page.tex('c')},<br>
                   ${page.tex('c^2 = a^2 + b^2 - 2\\cdot a \\cdot b \\cdot \\cos{\\alpha}')}`
            },
            repeatable_facts[' '],
            {
                name: ' , ,  ',
                text: '1.     <br>'+
                '2.      <br>'+
                '3.      '
            }
        ],
        '': [
            repeatable_facts['  '],
            {
                name: '  ',
                text: '        '
            },
            repeatable_facts[' '],
            repeatable_facts['  '],
            {
                name: '  ',
                text: '       ,      '
            }
        ],
        '': [
            {
                name: '  ',
                text: ',    ,  '
            },
            {
                name: '   ',
                text: ' ,  '
            },
            {
                name: '     ',
                text: '       ,<br>'+
                ' "" ,     ,   '
            },
            {
                name: '  ',
                text: ' ,    , '
            },
            {
                name: '    ',
                text: '       <br>'+
                '     '
            },
            {
                name: '   ',
                text: '1.       <br>'+
                '2.         '
            },
            repeatable_facts['']
        ],
        ' ': [
            repeatable_facts[' '],
            repeatable_facts[' '],
            {
                name: '  ',
                text: '      '
            },
            {
                name: '  ',
                text: '      ,   '
            }
        ],
        ' ': [
            {
                name: '  ',
                text: '    ,   '
            },
            {
                name: '   ',
                text: '     '
            },
            {
                name: '   ',
                text: ' ,    '
            },
            {
                name: '  ',
                text: ' ,        '
            }
        ],
        ' ': [
            repeatable_facts[' '],
            repeatable_facts[' '],
            {
                name: '  ',
                text: '       \\(180^{\\circ}\\)'
            },
            {
                name: '  ',
                text: ' ,       :<br>'+
                '1.     \\(180^{\\circ}\\)<br>'+
                '2.        '
            },
            {
                name: ' ',
                text: '        '
            }
        ],
        '': [
            {
                name:'  ',
                text: '    '
            },
            {
                name: '  ',
                text: '    '
            },
            {
                name: '   ',
                text: '        '
            },
            {
                name: '   ',
                text: '        '
            },
            {
                name: '  ',
                text: '    '
            },
            repeatable_facts['']
        ]
    };

    const parse = facts => {
        var keys = Object.keys(facts);
        var array = [];
        keys.forEach(key => {
            array.push({title: key, facts: facts[key]});
        });
        return array;
    };

    return page(
    <div>
        <h1 onClick={page.index} className='main-menu'></h1>
        <ul>
        {
            parse(facts).map((facts_group,index) =>
                <li key={index}>
                    <OpenList
                        header = {<div className="main-menu">{facts_group.title}</div>}
                        list = {
                            <ol>
                                {
                                    facts_group.facts.map((fact,index) =>
                                        <li key={index} style={{color: '#8c71fe'}}>
                                            <OpenList
                                                header = {<div className="sub-menu">{fact.name}</div>}
                                                list = {fact.text===null?<></>:
                                                    <div className="frame">
                                                        <h3 style={{marginTop:'-7px'}}>:</h3>
                                                        {
                                                            fact.text.split('<br>').map((str, index) =>
                                                                <div key={index}>{str===''?<span>&nbsp;</span>:str}</div>    
                                                            )
                                                        }
                                                    </div>
                                                }
                                            />
                                        </li>    
                                    )
                                }
                            </ol>
                        }
                    />
                </li>
            )
        }
        </ul>
    </div>
)};

export default Planimetry;