import { useRef, useState } from "react";

const Params = ({page}) => {

    const main = useRef();
    const analytic = useRef();
    const parabole = useRef();
    const change = useRef();
    const graph = useRef();
    const symmetry = useRef();
    const func = useRef();
    const evaluate = useRef();
    const refs = {
        analyticGeneral: useRef(),
        analyticSimle: useRef(),
        analyticInfty: useRef(),
        analyticSqr1: useRef(),
        analyticSqr2: useRef()
    }

    return page(
    <div>
        <h1 ref={main} onClick={page.index} className="main-menu">  </h1>
        <ul className="list">
            <li onClick={page.scroll(analytic)}></li>
            <li onClick={page.scroll(graph)}></li>
            <li onClick={page.scroll(parabole)}> </li>
            <li onClick={page.scroll(symmetry)}></li>
            <li onClick={page.scroll(change)}></li>
            <li onClick={page.scroll(func)}></li>
            <li onClick={page.scroll(evaluate)}></li>
        </ul>
           .     .
        <h2 onClick={page.scroll(main)} ref={analytic} className="main-menu"></h2>
        <div className="frame">
            <h3> :</h3>
                <Task
                    text={`   \\(x\\)  <br>\\(
                        <center>F(x, a)
                    \\)<br>    \\(a\\).`}
                />
            <h3>:</h3>
            <ol>
                <li>
                     {`\\(F(x, a) ~~~(*) \\LR
                        \\union{
                            G_1(x,a)~~~(1) \\\\
                            G_2(x,a)~~~(2) \\\\
                            \\ldots \\\\
                            G_n(x, a)~~~(n)
                        }
                    \\)`}.
                </li>
                <li>
                    <div>     {`\\(a\\)`}    {page.tex('G_i(x,a)')}.</div>
                    <div>{`\\(G_i(x,a)\\)`}      :</div>
                    <ul className="margin-bottom-list">
                    <li style={{marginTop:'40px'}}>
                            <div>{`\\(\\cases{\\cases{H(x,a) \\\\ a \\in A}~~(i.1) \\\\ f(x,a) \\neq 0}~~~\\)`},  {'\\(f(x,a)\\)'}   .</div>
                            <Hidden
                            childRef={refs.analyticGeneral}
                            block={<div ref={refs.analyticGeneral}>
                            <div> {`\\(C_k(a)~-\\)`}    {`\\(k\\)`}   {`\\(H(x,a)\\)`}.</div>
                            <div>:</div>
                            <div className="line flex">
                                <div>{`\\((i):~\\)`}</div>
                                <div>
                                <div className="flex">
                                    <div>{`\\((i.1):~\\)`}</div>
                                    <div>
                                        <div>{`\\(\\infty\\)  : \\(\\cases{C_{\\infty}(a) \\\\ a \\in A}\\)`}</div>
                                        <div>{`\\(n\\)  : \\(\\cases{C_n(a) \\\\ a \\in A}\\)`}</div>
                                        <div>{`\\((n-1)\\)  : \\(\\cases{C_{n-1}(a) \\\\ a \\in A}\\)`}</div>
                                        <div>...</div>
                                        <div>{`2  : \\(\\cases{C_2(a) \\\\ a \\in A}\\)`}</div>
                                        <div>{`1  : \\(\\cases{C_1(a) \\\\ a \\in A}\\)`}</div>
                                        <div>{`0  : \\(\\union{C_0(a) \\\\ a \\not\\in A}\\)`}</div>
                                    </div>
                                </div>
                                <div>
                                    <div>
                                         {`\\(a\\)`},   {`\\(f(x,a)=0\\)`}:
                                    </div>
                                    <div>{`\\(\\cases{H(x,a) \\\\ a\\in A \\\\ f(x,a)=0}\\)`}</div>
                                </div>
                                </div>
                            </div>
                            </div>}
                            />
                        </li>
                        <li>
                            <div>{`\\( \\cases{x = x(a) \\\\ a \\in A} \\).`}</div>
                            <Hidden
                            childRef={refs.analyticSimle}
                            block={<div ref={refs.analyticSimle}>
                            <div>:</div>
                            <div className="flex line">
                                <div>{`\\((i):~~\\)`}</div> <div>
                                    <div>1   {`\\(a \\in A\\)`}</div>
                                    <div>0   {`\\(a \\not\\in A\\)`}</div>
                                </div>
                            </div>
                            </div>}
                            />
                        </li>
                        <li>
                            <div>{`\\( \\cases{x \\in X \\\\ a \\in A} ~~~,\\)  \\(X ~- \\)  .`}</div>
                            <Hidden
                            childRef={refs.analyticInfty}
                            block={<div ref={refs.analyticInfty}>
                            <div>:</div>
                            <div>
                                <div className="flex line">
                                    <div>{`\\((i):~~\\)`}</div>
                                    <div>
                                        <div>{'\\(\\infty\\)'}   {`\\(a \\in A\\)`}</div>
                                        <div>0   {`\\(a \\not\\in A\\)`}</div>
                                    </div>
                                </div>
                                <div>     .</div>
                            </div>
                            </div>}
                            />
                        </li>
                        <li>
                            <div>{`\\(\\cases{\\cases{\\alpha x^2 + \\beta x + \\gamma = 0 \\\\ a\\in A}~~~(i.1) \\\\ f(x,a) \\neq 0}\\),  \\(f(x,a)\\)   .`}</div>
                            <Hidden
                            childRef={refs.analyticSqr1}
                            block={<div ref={refs.analyticSqr1}>
                            <div>:</div>
                            <div className="flex line">
                            <div>{`\\((i):~~\\)`}</div>
                            <div>
                                <div style={{display:'flex'}}>
                                <div>{`\\((i.1):~~\\)`}</div>
                                <div>
                                    <div>
                                         {`\\(\\alpha = 0 \\LR a \\in A_0:~~\\cases{
                                        \\beta x + \\gamma = 0 \\\\
                                        a \\in A \\\\
                                        \\alpha = 0
                                        }\\)`}
                                    </div>
                                    <div>
                                         {`\\(\\alpha \\neq 0:~~\\alpha x^2 + \\beta x + \\gamma = 0 ~-\\)`}  , 
                                        {`\\(~D=\\beta^2 - 4\\alpha\\gamma\\)`}
                                    </div>
                                    <div>
                                        2   {`\\(\\cases{D>0 \\\\ a \\in A \\\\ \\alpha \\neq 0}\\)`}
                                    </div>
                                    <div>
                                        1   {`\\(\\cases{D=0 \\\\ a \\in A\\\\ \\alpha \\neq 0}\\)`}
                                    </div>
                                    <div>
                                        0   {`\\(\\union{\\cases{D<0 \\\\ \\alpha \\neq 0} \\\\ a\\not\\in A}\\)`}
                                    </div>
                                </div>
                                </div>
                                <div>
                                     {`\\(a\\)`},   {`\\(f(x,a)=0\\)`}:
                                </div>
                                <div>{`\\(\\cases{\\alpha x^2 + \\beta x + \\gamma = 0 \\\\ a\\in A \\\\ f(x,a)=0}\\)`}</div>
                            </div>
                            </div>
                            </div>}
                            />
                        </li>
                        <li>
                            <div>{`\\(\\cases{\\cases{\\alpha x^2 + \\beta x + \\gamma = 0 \\\\ a\\in A}~~~(i.1) \\\\ f(x,a) \\neq 0}\\),  \\(f(x,a)\\)   .`}</div>
                            <Hidden
                            childRef={refs.analyticSqr2}
                            block={<div ref={refs.analyticSqr2}>
                            <div>:</div>
                            <div className="flex line">
                            <div>{`\\((i):~~\\)`}</div>
                            <div>
                                <div style={{display:'flex'}}>
                                <div>{`\\((i.1):~~\\)`}</div>
                                <div>
                                    <div>
                                        {'\\(\\alpha x^2 + \\beta x + \\gamma = 0~-\\)'}  {`\\(\\alpha=0\\)`} ;  {'\\(\\alpha\\neq 0\\)'} , {'\\(D=\\beta^2-4\\alpha\\gamma\\)'}.
                                    </div>
                                    <div>
                                        {'\\(\\infty\\)'}  : {`\\(
                                            \\cases{
                                                \\alpha = 0 \\\\
                                                \\beta = 0 \\\\
                                                \\gamma = 0 \\\\
                                                a\\in A
                                            }
                                        \\)`}
                                    </div>
                                    <div>
                                        2  : {`\\(
                                            \\cases{
                                                \\alpha \\neq 0 \\\\
                                                D > 0 \\\\
                                                a \\in A
                                            }
                                        \\)`}
                                    </div>
                                    <div>
                                        1  : {`\\(
                                            \\union{
                                                \\cases{
                                                    \\alpha = 0 \\\\
                                                    \\beta \\neq 0 \\\\
                                                    a \\in A
                                                } \\\\
                                                \\cases{
                                                    \\alpha \\neq 0 \\\\
                                                    D = 0 \\\\
                                                    a \\in A
                                                }
                                            }
                                        \\)`}
                                    </div>
                                    <div>
                                        0  : {`\\(
                                            \\union{
                                                \\cases{
                                                    \\alpha = 0 \\\\
                                                    \\beta = 0 \\\\
                                                    \\gamma \\neq 0
                                                }\\\\
                                                \\cases{
                                                    \\alpha \\neq 0 \\\\
                                                    D < 0
                                                }\\\\
                                                a \\not\\in A
                                            }
                                        \\)`}
                                    </div>
                                </div>
                                </div>
                                <div>
                                     {`\\(a\\)`},   {`\\(f(x,a)=0\\)`}:
                                </div>
                                <div>{`\\(\\cases{\\alpha x^2 + \\beta x + \\gamma = 0 \\\\ a\\in A \\\\ f(x,a)=0}\\)`}</div>
                            </div>
                            </div>
                            </div>}
                            />
                        </li>
                    </ul>
                </li>
                <li>
                    <div>   , :</div>
                    <div className="line">
                        <div>  ({`\\(i\\)`})  ({`\\(j\\)`}):</div>
                        <div>{page.tex('\\cases{G_i(x,a) \\\\ G_j(x,a)}')}</div>
                    </div>
                </li>
                <li>
                       (1), (2),..., ({`\\(n\\)`})
                      {`\\(a\\)`}   .   .
                </li>
                <li>
                       {`\\((*)\\)`}   {`\\(a\\)`}    .
                </li>
            </ol>
        </div>
        <h2 ref={graph} onClick={page.scroll(main)} className="main-menu"></h2>
        <div className="frame">
            <h3> :</h3>
            <Task
                text={`
                       \\(x\\)  <br>
                    <center>\\(F(x,y,a)\\)<br>
                        \\(a\\).
                `}
            />
            <h3>:</h3>
            <ol className="margin-bottom-list">
                <li>
                     {`\\(F(x,y,a) \\LR \\cases{G(x,y)~~~~~~~(1) \\\\ A(x,y,a)~~~~(2)} \\)`}.
                </li>
                <li>
                        (1).
                </li>
                <li>
                      (2)      {`\\(a\\)`}.
                </li>
                <li>
                        (2)  {`\\(a = -\\infty\\)`}.
                </li>
                <li>
                    <div>  {`\\(a\\)`},     (2),</div>
                    <div>       (1) , ,</div>
                    <div>    {`\\(a\\)`},   .</div>
                </li>
                <li>
                          {`\\(a\\)`}   .
                </li>
            </ol>
        </div>
        <h2 ref={parabole} onClick={page.scroll(main)} className="main-menu"> </h2>
        <div className="frame">
            <h3> :</h3>
                <Task
                    text = {`     <br>\\(
                        <center>\\alpha x^2 + \\beta x + \\gamma \\vee 0
                    \\)<br>    ,   \\(x_i\\).`}
                />
            <h3>:</h3>
            <ol>
                <li>  (1): {'\\(\\alpha = 0\\)'}</li>
                <li>  (2): {'\\(\\alpha \\neq 0\\)'}</li>
                <li>
                    <div>:</div>
                        <ul>
                            <li>{'\\(f(x) = \\alpha x^2 + \\beta x + \\gamma ~-\\) '}</li>
                            <li>{'\\(D = \\beta^2 - 4\\alpha\\gamma\\)'}</li>
                            <li>{' \\(x_ = \\frac{-\\beta}{2\\alpha}\\)'}</li>
                            <li>{'\\(f(x_i) = \\alpha x_i^2+\\beta x_i+\\gamma\\)'}</li>
                        </ul>
                </li>
                <li>
                    <div> :</div>
                    <div>(2.1) {'\\(D<0;~~\\)'}   {'\\(\\alpha\\)'}   </div>
                    <div>(2.2) {'\\(D=0;~~\\)'}   {'\\(x_,~\\alpha\\)'}   </div>
                    <div>
                        (2.3) {'\\(D>0\\)'}
                        <div style={{marginLeft:'30px'}}>
                            (2.3.1) {'\\(x_ \\vee x_i;~~\\)'}   {'\\(\\alpha\\cdot f(x_i)\\)'}   
                        </div>
                    </div>
                </li>
                <li>
                    <div>       .</div>
                </li>
                <li>
                    <div>    :</div>
                    <ul>
                        <li>
                            <div>{'\\(\\alpha >0~~-\\)   '}</div>
                            <div>{'\\(\\alpha <0~~-\\)   '}</div>
                        </li>
                        <li>
                            <div>{'\\(D<0~~-\\)        '}</div>
                            <div>{'\\(D=0~~-\\)      '}</div>
                            <div>{'\\(D>0~~-\\)    '}</div>
                        </li>
                        <li>
                            <div>{'\\(\\alpha\\cdot f(x_i)<0~~-\\)     \\(x_i\\)'}</div>
                            <div>{'\\(\\alpha\\cdot f(x_i)>0~~-\\)     \\(x_i\\)'}</div>
                            <div>{'\\(\\alpha\\cdot f(x_i)=0~~-\\)      \\(x_i\\)'}</div>
                        </li>
                    </ul>
                </li>
            </ol>
        </div>
        <h2 ref={symmetry} onClick={page.scroll(main)} className="main-menu"></h2>
        <div className="frame">
            <h3> :</h3>
            <Task
                text={`
                     \\(a\\) <br>
                <center>\\(F(x,a)\\)<br>
                  \\(n\\) .
                `}
            />
            <h3>:</h3>
            <ol>
                <li>
                    <div>   {`\\(f(x)\\)`},  {'\\(F(f(x),a) \\LR F(x,a)\\)'}. :</div>
                    <div className="line">
                        <div> {'\\(x~-\\)'} ,  {'\\(f_1(x),f_2(x),\\ldots, f_k(x)~-\\)'}  ,</div>
                        <div>  {`\\(F(x,a) \\LR F(f_i(x), a)\\)`}.</div>
                    </div>
                </li>
                <li>
                    <div className="line">
                        <div>  {`\\(x,~f_1(x),~f_2(x),\\ldots,f_k(x):~~k+1\\)`},</div>
                        <div> {'\\(n\\)'} ,  {'\\(n \\ndiv (k+1)~\\Rightarrow\\)'} -  .</div>
                    </div>
                </li>
                <li>
                    <div>   , :</div>
                    <div className="line">
                        <div> :</div>
                        <div>1) {'\\(\\cases{ F(x,a)\\\\ x = f_i(x) }\\)'}</div>
                        <div>2) {'\\(\\cases{ F(x,a)\\\\ f_i(x) = f_j(x) }\\)'}</div>
                    </div>
                </li>
                <li>
                    <div>      {'\\(a\\)'}  {'\\(F(x,a)\\)'}.</div>
                    <div> {'\\(a\\)'},     {'\\(n\\)'},   . </div>
                </li>
            </ol>
        </div>
        <h2 ref={change} onClick={page.scroll(main)} className="main-menu"></h2>
        <div className="frame">
            <ol>
                <li>
                    <h3>  </h3>
                    <div className="line">
                        {`\\(
                           F(x, a, f(x,a)) \\overset{t=f(x,a)}{\\LLR}
                           F(x,a,t) 
                        \\)`}
                    </div>
                    <ul>
                        <li>   .</li>
                        <li>    .</li>
                    </ul>
                </li>
                <li style={{marginTop:'50px'}}>
                    <h3>   </h3>
                    <div className="line flex">
                        <div>
                            <div>{`\\(F(f(x,a),a)\\)`}</div>
                            <div>: {`\\(t = f(x,a)\\)`}</div>
                            <div>{`\\(F(t,a)\\)`}</div>
                        </div>
                        <div>
                        <table style={{fontSize:'15pt', marginLeft:'10px'}}>
                                <tbody>
                                    <tr>
                                        <th> {'\\(t\\)'}</th>
                                        <th>  {'\\(x\\)'}</th>
                                    </tr>
                                    <tr>
                                        <td>{'\\(T_1\\)'}</td>
                                        <td>{'\\(I_1\\)'}</td>
                                    </tr>
                                    <tr>
                                        <td>{'\\(T_2\\)'}</td>
                                        <td>{'\\(I_2\\)'}</td>
                                    </tr>
                                    <tr>
                                        <td>{'\\(\\ldots\\)'}</td>
                                        <td>{'\\(\\ldots\\)'}</td>
                                    </tr>
                                    <tr>
                                        <td>{'\\(T_n\\)'}</td>
                                        <td>{'\\(I_n\\)'}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                    <ul>
                        <li>       .</li>
                        <li>   .</li>
                    </ul>
                </li>
                <li style={{marginTop:'50px'}}>
                    <h3>   </h3>
                    <div className="line">
                    {'\\(F(x,f(a)) ~~~\\overset{b = f(a)}{\\LLR}~~~F(x,b)\\)'}
                    </div>
                    <ul>
                        <li>
                                 .
                        </li>
                        <li>
                                      .
                        </li>
                    </ul>
                </li>
                <li style={{marginTop:'50px'}}>
                    <h3>/ </h3>
                    <div className="line">
                    {'\\(F(x,f(x,a),a) \\Leftarrow \\cases{F(x,y,a) \\\\ y=f(x,a)}\\)'}
                    </div>
                    <ul>
                        <li>  ,   .</li>
                        <li> :      .</li>
                        <li> :       .</li>
                        <li>   .</li>
                    </ul>
                </li>
            </ol>
        </div>
        <h2 ref={func} onClick={page.scroll(main)} className="main-menu"></h2>
        <div className="frame">
            <h3>   :</h3>
            <ul>
                <li>
                      {`\\(y=f(x,a)\\)`}     {`\\(\\varphi(x)=f(x,a)\\)`}.
                </li>
                <li>
                      {`\\(f(u) \\vee f(v)\\)`}  ,  {`\\(f(x)\\)`}  .
                </li>
                <li>
                    {`\\(y=f(x) \\LR \\cases{y=f(x)\\\\ y \\in E(f)}~~\\)`},  {`\\(E(f)~-\\)`}   .
                </li>
            </ul>
        </div>
        <h2 ref={evaluate} onClick={page.scroll(main)} className="main-menu"></h2>
        <div className="frame">
            <h3>     -:</h3>
            <ul>
                <li>
                    {`\\(u^{2n} \\geq 0\\)`}
                </li>
                <li>
                    {`\\(|u| \\geq 0\\)`}
                </li>
                <li>
                    {`\\(-1 \\leq \\sin{u} \\leq 1\\)`}
                </li>
                <li>
                    {`\\(-1 \\leq \\cos{u} \\leq 1\\)`}
                </li>
            </ul>
        </div>
        <div style={{height:`1000px`}}></div>
    </div>
)};

const Task = ({text, block = undefined}) => (
    <div className="param-task">
        <div style={{marginTop:'-5px'}}>
        {
            text.split('<br>').map((str, i) => 
            <div key={i} style={{display:'flex', justifyContent:str.indexOf('<center>')===-1?'start':'center'}}>
                <div style={{width:'fit-content'}}>{str.replace('<center>','')}</div>
            </div>
            )
        }
        </div>
        {block}
    </div>
);
const Hidden = ({block, childRef}) => {
    
    const [hidden, setHidden] = useState(true);

    return <div>
        <div onClick={() => setHidden(h => !h)} className="sub-menu">
            {hidden?' ':' '}
        </div>
        <div style={{transition:'max-height 0.5s ease',overflow:'hidden',maxHeight:`${hidden?0:childRef.current.scrollHeight+10}px`}}>
            {block}
        </div>
    </div>
};

export default Params;