import Lessons from "./Lessons";
import OpenList from "./OpenList";
import Timer from "./Timer";
import Timetable from "./Timetable";

const IndexPage = ({page}) => {

    return page(
        <div>
            <div>
            <ul className="indexPageList">
                <li><h1 onClick={page.window('https://angrybro.github.io/conspects/math/old')}> </h1></li>
                <li>
                    <OpenList
                        header={<h1></h1>}
                        list={
                            <ol className="list">
                                <li onClick={page.navigate('')}></li>
                                <li onClick={page.navigate('')}> </li>
                                <li onClick={page.navigate('')}>  </li>
                            </ol>
                        }
                    />                    
                </li>
                <li>
                    <OpenList
                        header={<h1> </h1>}
                        list={
                            <ul className="list">
                                <li style={{border:'none'}}><div style={{marginLeft:'-10px'}} className="main-menu"  onClick={page.window("https://docs.google.com/spreadsheets/d/1S7MAjub53zNpa9PArF74OWQ8Qqlzr76gK2XtYyeSu54/edit?usp=sharing")}>  </div></li>
                                <li onClick={page.window("https://drive.google.com/file/d/1JqqttNjcHAUipunroOwOfp4I1id3y5ZP/view?usp=sharing")}></li>
                                <li onClick={page.window("https://drive.google.com/file/d/1v_cgdkeKW03JlPVvxZvplHRBMKDqhNV6/view?usp=sharing")}></li>
                                <li onClick={page.window("https://drive.google.com/file/d/1YLCUCOj8bs0JTuM0i8BWKWl6-vvrT350/view?usp=sharing")}></li>
                                <li onClick={page.window("https://drive.google.com/file/d/1HmD2COo8s5fM6D0D4Mw9l6-LDGKSdzDX/view?usp=sharing")}></li>
                                {/* <li onClick={page.window('404')}></li> */}
                                <li onClick={page.window("https://drive.google.com/file/d/1MBu-hVrBRWgctBSSwlTT32BHfh3RKLED/view?usp=sharing")}></li>
                                {/* <li onClick={page.window('404')}> </li> */}
                            </ul>
                        }
                    />
                </li>
                <li hidden={true}>
                    <OpenList
                        header={<h1> </h1>}
                        list={
                            <Timetable
                                groups={[
                                    {number: 35, days: ', ', time: '15:00'},
                                    {number: 36, days: ', ', time: '15:00'},
                                    {number: 37, days: ', ', time: '15:00'}
                                ]}
                            />
                        }
                    />
                </li>
                <li>
                    <OpenList
                        header={<h1> </h1>}
                        list={<Lessons/>}
                    />
                </li>
                <li onClick={page.window("https://docs.google.com/spreadsheets/d/1ymkx7Tbok4JtQovr6okvu_1bpVVptfb1myr_Rn2VuQI/edit?usp=sharing")}><h2>   </h2></li>
                <li onClick={page.window("https://drive.google.com/drive/folders/1xL7xy0TNA6vJ0Plzh84lB54qohrkL4WA?usp=sharing")}><h2> </h2></li>
                <li onClick={page.window("https://angrybro.github.io/checkege")}><h2> </h2></li>  
                <li onClick={page.window("https://angrybro.github.io/LatexRealTime")}><h2> </h2></li>        
            </ul>
            </div>
            <div style={{position:'absolute', top:'70px', left:'700px'}}>
                <Timer/>
            </div>
        </div>
    );
};

export default IndexPage;